import { Navigate, Route, Routes } from "react-router-dom";
import { Pokedex } from "./Pages/Pokedex";
import { PokeInfo } from "./Pages/PokeInfo";
import { useEffect, useState } from "react"
import { usePromiseAll } from "./Hook/usePromiseAll"


function App() {
  const { promises } = usePromiseAll()
  const [pokeName, setPokeName] = useState("");
  const [pokemones, setPokemones] = useState([]);

  useEffect(() => {
    setPokemones(promises)
  }, [promises, pokeName])

  return (

    <Routes>
      <Route exact path={`/`} element={<Navigate to={`pokemon-filter/pokemones`}/>}/>
      <Route exact path={`pokemon-filter/pokemones`} element={<Pokedex setPokeName={setPokeName} pokeName={pokeName} pokemones={pokemones} />}/>
      <Route path={`/pokemon-filter/pokemones/:PokemonName`} element={<PokeInfo pokemones={pokemones} setPokeName={setPokeName} />}/>
      <Route path="*" element={<Navigate to={`/`}/>} />
    </Routes>
  );
}

export default App;
